body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
  margin: 0px;
  padding: 0px;
}

p {
  margin: 0px
}

.wrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 60vh 20vh;
  justify-items: center;
  -webkit-align-items: center;
          align-items: center;
  border: 1px solid black;
  max-width: 60%;
  margin-right: auto;
  margin-left: auto;
}

#surf-score {
  font-size: 10em;
}

#summary {
  /* background-image: url('../resources/buoy.png'); */
  /* background: white; */
  grid-column: 1/4;
}

.property {
  width: 100%;
  height: 100%;
  border: 1px solid black;
  text-align: center;
  margin: 0px;
  display: grid;
  grid-template-rows: 20% 60% 20%;
  -webkit-align-items: center;
          align-items: center;
  justify-items: center;
}

.property p {
  border: 1px solid black;
  width: 100%;
  height: 100%;
  margin: 5px;
  display: grid;
  -webkit-align-items: center;
          align-items: center;
}

